<template>
  <v-navigation-drawer
    v-model="drawer"
    :color="color"
    :expand-on-hover="expandOnHover"
    :mini-variant="miniVariant"
    :right="right"
    :permanent="permanent"
    :src="bg"
    absolute
    app
  >
    <v-list dense nav class="py-0">
      <v-list-item two-line :class="miniVariant && 'px-0'">
        <v-list-item-avatar>
          <img src="https://randomuser.me/api/portraits/men/81.jpg" />
        </v-list-item-avatar>

        <v-list-item-content>
          <v-list-item-title>Application</v-list-item-title>
          <v-list-item-subtitle>Subtext</v-list-item-subtitle>
        </v-list-item-content>
      </v-list-item>

      <v-divider></v-divider>

      <template v-for="item in items">
        <!--  -->
        <v-list-item 
          v-if="!item.children" 
          :key="item.title" 
          :to="item.link"
          link
          >
          <!--  -->
          <v-list-item-icon>
            <v-icon :color="item.color ? item.color : ''">
              {{ item.icon }}
            </v-icon>
          </v-list-item-icon>
          <!--  -->
          <v-list-item-content>
            <v-list-item-title>
              {{ item.title }}
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <!--   end -->

        <!--  -->
        <v-list-group
          v-else
          :key="item.title"
          :prepend-icon="item.icon"
          no-action
        >
          <!--  -->
          <template v-slot:activator>
            <v-list-item-content>
              <v-list-item-title>
                {{ item.title }}
              </v-list-item-title>
            </v-list-item-content>
          </template>

          <!--  -->
          <v-list-item
            v-for="child in item.children"
            :key="child.title"
            :to="child.link"
            link
          >
            <v-list-item-content>
              <v-list-item-title>
                {{ child.title }}
              </v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-group>
      </template>
    </v-list>
  </v-navigation-drawer>
</template>


      <!-- <v-list-item
        v-for="item in items"
        :key="item.title"
        link
        :to="item.to"
        active-class="accent"
      >
        <v-list-item-icon>
          <v-icon>{{ item.icon }}</v-icon>
        </v-list-item-icon>

        <v-list-item-content>
          <v-list-item-title>{{ item.title }}</v-list-item-title>
        </v-list-item-content>
      </v-list-item> -->
    </v-list>
  </v-navigation-drawer>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  name: "DashboardDrawer",
  data() {
    return {
      //   drawer: true,
      // items: [
      //   { title: "Dashboard", icon: "mdi-view-dashboard", to: "/" },
      //   { title: "Photos", icon: "mdi-image", to: "/pages/photos" },
      //   { title: "About", icon: "mdi-help-box" },
      // ],
      items: [
        {
          title: "Dashboard",
          icon: "mdi-home",
          link: "/dashboard",
          chip: "new",
        },
        {
          title: "Photos",
          icon: "mdi-image",
          link: "/pages/photos",
          chip: 10,
        },
        // { title: "Tables", icon: "mdi-grid-large", link: "/tables" },
        // {
        //   title: "Notifications",
        //   icon: "mdi-bell-outline",
        //   link: "/notifications",
        // },
        {
          title: "Products",
          icon: "mdi-sprout-outline",
          link: "/products",
          active: false,
          children: [
            {
              title: "",
              icon: "mdi-circle-small",
              link: "/products/index",
              chip: 6,
            },
            {
              title: "",
              icon: "mdi-bell-outline",
              link: "/products/category",
            },
            {
              title: "",
              icon: "mdi-circle-small",
              link: "/products/property",
            },
          ],
        },
        // { divider: true },
        // { heading: "HELP" },
        {
          title: "Library",
          icon: "mdi-book-variant-multiple",
          link: "/books",
          active: false,
          children: [
            {
              title: "All Books",
              icon: "mdi-book-multiple-outline",
              link: "",
            },
            {
              title: "Books Category",
              icon: "mdi-book-check-outline",
              link: "",
            },
            {
              title: "Book Property",
              icon: "mdi-book-open-blank-variant",
              link: "",
            },
          ],
        },
        { title: "Support", icon: "mdi-forum" },
        { title: "FAQ", icon: "mdi-help-circle-outline" },
        // { divider: true },
        // { heading: "PROJECTS" },
        { title: "My recent", icon: "mdi-circle-medium", color: "warning" },
        { title: "Starred", icon: "mdi-circle-medium", color: "primary" },
        { title: "Background", icon: "mdi-circle-medium", color: "error" },
      ],
      color: "",
      colors: ["primary", "blue", "success", "red", "teal"],
      right: false,
      permanent: false,
      miniVariant: false,
      expandOnHover: false,
      background: false,
    };
  },
  computed: {
    bg() {
      return this.background
        ? "https://cdn.vuetifyjs.com/images/backgrounds/bg-2.jpg"
        : undefined;
    },
    drawer: {
      get() {
        return this.$store.state.drawer;
      },
      set(val) {
        this.$store.commit("SET_DRAWER", val);
      },
    },
  },
};
</script>

